@import "settings";

// common css code

*
{
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $body_bg;
  overflow-x: hidden;
  padding-bottom:96px;

  font-size: 16px;
  line-height: 1.4;
  font-weight: 500;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}


.app
{
  //max-width: $wrapper_width;
  margin: 0 auto;
  min-height:100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows:  auto auto 1fr  auto;
  grid-template-areas:
          "header header"
          "main main"
          "footer footer";
  grid-gap: 1px;
  background-color: $border-color;

  @media(max-width: 500px)
  {
    grid-template-columns: 1fr 1fr;
    grid-template-rows:  auto auto 1fr auto;
    grid-template-areas:
            "header header"
            "main main"
            "footer footer";
  }


  header
  {
    grid-area: header;
  }

  main
  {
    background-color: $body_bg;
  }

  header, footer
  {
    background-color: white;
  }

  aside
  {
    grid-area: sidebar;
    min-width: $aside_width;
  }
}

.app > *
{
  //padding:$gutter-space;
  background-color: $body_bg;
}


main
{
  padding: 0 !important;
  grid-area: main;
}

footer
{
  grid-area: footer;
  text-align: center;
}

/// header css

.header
{
  display: flex;
  align-items:center;
  $this: &;

  .container
  {
    padding: 0;
  }

  &__logo
  {
    img
    {
      max-width: 200px;
    }

    + span
    {
      text-transform: capitalize;
      font-size: 30px;
      color: $black;
      margin-left: $gutter-space;
      position: relative;
      &:before
      {
        content: '';
        position: absolute;
        width: 1px;
        height: 150%;
        background: #ddd;
        left: -20px;
        top: 0;
        bottom: 0;
        margin: auto;
      }
    }
  }

  &__links
  {
    display: flex;
    align-items:center;
    $this: &;
    > div{
      margin-right: $gutter-space;
    }
    &__search
    {
    }
  }
}


.container
{
  padding-left: 15px;
  padding-right: 15px;
  margin: 0 auto;


  @media(min-width: 320px)
  {
    width: 100%;
  }

  @media(min-width: 767px)
  {
    width: 100%;
  }

  @media(min-width: 992px)
  {
    width: 100%;
  }

  @media(min-width: 1200px)
  {
    max-width: 1170px;
    width: 100%;
  }

  @media(min-width: 1600px)
  {
    max-width: 1440px;
    width: 1440px;
  }


}

section
{
  width: 100%;
  padding:$gutter-space*2 $gutter-space;
}




/// banner section

.banner
{
  $this: &;
  min-height:calc(100vh - 85px);
  width:100%;
  position: relative;
  display: flex;
  align-items:center;

  &:before
  {
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.4);
    z-index: 2;
  }

  img
  {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index:1;
    -o-object-fit: cover;
    object-fit: cover;
   // filter:blur(5px);
  }

  &__content
  {
    display: flex;
    flex-wrap: wrap;
    align-items:center;
    justify-content: space-between;
    position: relative;
    z-index:3;
    color: $white;
    font-size:18px;
    line-height:1.4;
  }

  &__leftContent
  {
    max-width: 620px;
    flex:1;
    padding-right:$gutter-space;
  }

  &__enquireyForm
  {
    background-color: $white;
    padding: $gutter-space;
    border-radius: $border_radius_base;
    color: $black;
    max-width:400px;

    $this: &;
    &__title
    {

    }

    @media(max-width: 886px)
    {
      max-width: 100%;
    }

    .btn
    {
      margin-top: $gutter-space;
    }
  }

  &__links
  {
    display: flex;
    align-items:center;
    list-style:none;
    padding-left: 0;
    margin-top:$gutter-space*3;
    margin-left: -$gutter-space/2;
    margin-right: -$gutter-space/2;

    @media(max-width: 886px)
    {
      margin-top:$gutter-space/2;
    }
    $this: &;
    &__item
    {
      $this: &;

      &:not(:first-of-type)
      {
        border-left:2px solid $white;
      }
      &__link
      {
        text-decoration: none;
        padding-left: $gutter-space/2;
        padding-right: $gutter-space/2;
        color: $white;
        font-weight: bold;
        display: inline-block;
        font-style: 16px;
        //border-left:2px solid $white;
      }
    }
  }
  
  &__offer
  {
    margin-top:$gutter-space;
    background-color: rgba(255,255,255,0.2);
    padding:$gutter-space/2;
    font-weight:bold;
    text-transform: capitalize;
    display: inline-block;
    margin-bottom: $gutter-space;
  }
}


.form-group
{
  margin-bottom:$gutter-space/2;

  .form-control
  {
    width: 100%;
    border-radius: $border-radius_base;
    border: 1px solid $gray;
    box-sizing:border-box;
    padding:8px 15px;
  }
}


.sectionTitle
{
  color: #232323;
  font-size: 32px;
  text-transform: uppercase;
  font-weight:bold;
  text-align: center;
  margin-bottom:$gutter-space*1.5;
  position: relative;
  &:before{
    content: '';
    width: 100px;
    height:5px;
    background-color: #8c1515;
    position: absolute;
    bottom: -10px;
    left: 0;
    right:0;
    margin:auto;
  }

  &.small
  {
    font-size:20px;
    margin-bottom: $gutter-space;
  }

  &.gray
  {
    &:before{
      background-color: $gray;
    }
  }
}

.wu
{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: $gutter-space*2;
  margin:$gutter-space 0;
  $this: &;

  @media(max-width: 992px)
  {
    grid-template-columns: repeat(2, 1fr);
  }


  @media(max-width: 767px)
  {
    grid-template-columns: repeat(1, 1fr);
  }



  &__box
  {

    background-color: $white;
    box-shadow: 3px 3px 18px 0 rgba(0, 0, 0, 0.24);;
    padding: $gutter-space*2;
    text-align: center;
    $this: &;

    &__img
    {
      margin-bottom: $gutter-space;
    }

    &__content
    {
      line-height:1.4;
      font-size:20px;
    }
  }
}

.curriculum{
  background-color: #e2ddcb;
}

.accordion {
  background-color: $white;
  margin-top:$gutter-space*2;

  .btn
  {
    color: $black;
    position: relative;
    width: 100%;
    height: 100%;
    margin: 0;
    background-color: #fff;
    padding: 25px 15px;
    text-align: left;
    border: 0;
    text-transform: capitalize;
    font-size:20px;
    font-weight: 500;
    cursor: pointer;

    &:after{
      content: '+';
      position: absolute;
      right:20px;
    }

    &.active
    {
      background-color: lighten($gray, 10%);
      + .collapse
      {
        display: block;
      }

      &:after{
        content: '-';
      }
    }


  }
}


.collapse {
  display: none;
  background-color: #fff;
  padding: 20px;
  border-top: 1px solid #ddd;
  border-bottom: 1px solid #ddd;
}


footer
{
  background-color: $gray;
}


.text-center
{
  text-align: center !important;
}


.animatedBar
{
  position: fixed;
  bottom: 0;
  width: 100%;
  background: #8c1515;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: $gutter-space/2;
  color: white;
  font-size: 21px;
  text-transform: capitalize;
  p
  {
    margin-right:$gutter-space;
  }
}


.embed-responsive
{
  position: relative;
  padding-bottom: 56.25%; /* 16:9 */
  height: 0;
  iframe
  {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

.form-check
{
  display: flex;
  font-size:14px;
  font-weight: normal;
  $this: &;

  label{
    padding-left: 10px;
  }
  &-input
  {
    margin-top: 5px;
  }
}