// button css

.btn {
  padding: $padding_x $padding_y;
  border-radius: $border-radius_base;
  box-shadow: none;
  border: 0;
  cursor: pointer;
  font-weight: bold;
  -webkit-transition: all 0.1s;
  -moz-transition: all 0.1s;
  -ms-transition: all 0.1s;
  -o-transition: all 0.1s;
  transition: all 0.1s;
  font-size: 12px;
  text-transform: capitalize;
  color: $white;

  &.btn-lg
  {
    padding: $padding_x*1.5 $padding_y*1.5;
    font-size:16px;
  }

  &.btn-danger
  {
    background: $danger;
  }

  &.btn-warning
  {
    background-color: $warning;
    color:$black;
  }
}
